<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-table :data="rightList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level==0"></el-tag>
            <el-tag type="success" v-else-if="scope.row.level==1"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightList: []
    };
  },
  created() {
    this.getRightsList();
  },
  methods: {
    async getRightsList() {
      const { data: res } = await this.$http.get(`rights/list`);
      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      this.rightList = res.data;
    }
  }
};
</script>