<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRolesDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!--  -->
            <el-row
              v-for="(item1,i1) in scope.row.children"
              :class="['tagBottom','vcenter',i1==0?'tagTop':'']"
              :key="item1.id"
            >
              <el-col :span="5">
                <el-tag closable @close="removeRightByid(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->

              <el-col :span="19">
                <el-row
                  v-for="(item2,i2) in item1.children"
                  :key="item2.id"
                  :class="[i2==0?'':'tagTop','vcenter']"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightByid(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightByid(scope.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="500px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              @click="editRoles(scope.row.id)"
              size="mini"
              icon="el-icon-edit"
            ></el-button>
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="deleteRoles(scope.row.id)"
            ></el-button>
            <el-button
              @click="showSetRightDialog(scope.row)"
              type="warning"
              size="mini"
              icon="el-icon-setting"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRolesDialogVisible"
      width="50%"
      @close="addRolesDialogClose"
      :close-on-click-modal="false"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editRolesDialogVisible"
      width="50%"
      @close="editRolesDialogClose"
      :close-on-click-modal="false"
    >
      <el-form :model="editRolesInfo" :rules="addFormRules" ref="editFormRef" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRolesInfo.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRolesInfo.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="setRightdialogVisible" width="50%">
      <el-tree
        :data="rightList"
        :props="treeProps"
        node-key="id"
        show-checkbox
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightdialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      // 
      addRolesDialogVisible: false,
      // 
      editRolesDialogVisible: false,
      // 
      addForm: {},
      // 
      addFormRules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 1  10 ", trigger: "blur" }
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 16, message: " 2  16 ", trigger: "blur" }
        ]
      },
      // 
      editRolesInfo: {},
      // 
      setRightdialogVisible: false,
      // 
      rightList: [],
      treeProps: {
        label: "authName",
        children: "children"
      },
      //id
      defKeys: [],
      // id
      roleId: ""
    };
  },
  created() {
    this.getRouList();
  },
  methods: {
    // 
    async getRouList() {
      const { data: res } = await this.$http.get(`roles`);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.rolesList = res.data;
      console.log(this.rolesList);
    },
    //
    addUser() {
      this.$refs.addFormRef.validate(async vaild => {
        if (vaild) {
          const { data: res } = await this.$http.post(`roles`, this.addForm);
          if (res.meta.status !== 201) {
            return this.$message.error("");
          }
          this.$message.success(res.meta.msg);
          this.addRolesDialogVisible = false;
          this.getRouList();
        }
      });
    },
    // 
    addRolesDialogClose() {
      this.$refs.addFormRef.resetFields();
    },
    editRolesDialogClose() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    async editRoles(id) {
      this.editRolesDialogVisible = true;
      const { data: res } = await this.$http.get(`roles/${id}`);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.editRolesInfo = res.data;
    },
    async editUser() {
      const { data: res } = await this.$http.put(
        `roles/${+this.editRolesInfo.roleId}`,
        {
          roleName: this.editRolesInfo.roleName,
          roleDesc: this.editRolesInfo.roleDesc
        }
      );

      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      this.$message.success("");
      this.editRolesDialogVisible = false;
      this.getRouList;
    },
    // 
    deleteRoles(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete(`roles/${id}`);
          if (res.meta.status !== 200) {
            return this.$message({
              type: "error",
              message: "!"
            });
          }
          this.$message({
            type: "success",
            message: "!"
          });
          this.getRouList();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    removeRightByid(role, right) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete(
            `roles/${role.id}/rights/${right}`
          );

          if (res.meta.status !== 200) {
            return this.$message({
              type: "error",
              message: "!"
            });
          }
          this.$message({
            type: "success",
            message: "!"
          });
          console.log(role);
          role.children = res.data;

          // this.getUserList();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    async showSetRightDialog(node) {
      // ;
      this.roleId = node.id;
      this.defKeys = [];
      this.setRightdialogVisible = true;
      const { data: res } = await this.$http.get(`rights/tree`);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      console.log(this.rightList);
      this.getLeafKeys(node, this.defKeys);
      this.rightList = res.data;
    },
    // id
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr);
      });
    },
    // 
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getHalfCheckedKeys(),
        ...this.$refs.treeRef.getCheckedKeys()
      ];
      var keyString = keys.join(",");
      console.log(keyString);
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: keyString }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.setRightdialogVisible = false;
      this.getRouList();
    }
  }
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.tagTop {
  border-top: #ddd solid 1px;
}
.tagBottom {
  border-bottom: solid 1px #ddd;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>