<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" clearable @clear="searchUser" v-model="queryInfo.query">
            <el-button slot="append" @click="searchUser" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addUserDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userSwitch(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
              size="mini"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              @click="deleteUser(scope.row.id)"
              icon="el-icon-delete"
              size="mini"
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              :enterable="false"
              placement="top"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                @click="setRole(scope.row)"
                size="mini"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addUserDialogVisible"
        width="50%"
        @close="addUserDialogClose"
        :close-on-click-modal="false"
      >
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addUserDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser()"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :close-on-click-modal="false"
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form :model="editForm" :rules="editRules" ref="editRef" label-width="70px">
          <el-form-item label="">
            <el-input disabled v-model="editForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo()"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRoledialogVisible"
        :close-on-click-modal="false"
        width="50%"
      >
        <p>{{userRole.username}}</p>
        <p>{{userRole.role_name}}</p>
        <p>
          
          <el-select v-model="rouleSelect" placeholder="">
            <el-option
              v-for="item in roulesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoledialogVisible = false"> </el-button>
          <el-button type="primary" @click="setRoleHandel"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkMobile = (rule, value, cb) => {
      const mobileRE = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (mobileRE.test(value)) {
        return cb();
      } else {
        return cb(new Error(""));
      }
    };
    // 
    var checkEmail = (rule, value, cb) => {
      const emailRE = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (emailRE.test(value)) {
        return cb();
      } else {
        return cb(new Error(""));
      }
    };
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      userlist: [],
      total: 0,
      // 
      addUserDialogVisible: false,
      // 
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 16, message: " 6  16 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      // 
      setRoledialogVisible: false,
      userRole: {},
      roulesList: [],
      rouleSelect: ""
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get("users", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) return this.$message("");
      this.userlist = res.data.users;
      this.total = res.data.total;
      console.log(res);
    },
    // handleSizeChange
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    // 
    async userSwitch(data) {
      const { data: res } = await this.$http.put(
        `users/${data.id}/state/${data.mg_state}`
      );
      if (res.meta.status !== 200) {
        $data.mg_state = !$data.mg_state;
        return this.$message.error("");
      }
      this.$message.success("");
    },
    // 
    searchUser() {
      this.getUserList();
    },
    // 
    addUserDialogClose() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async vaild => {
        if (vaild) {
          const { data: res } = await this.$http.post(`users`, this.addForm);
          if (res.meta.status == 201) {
            this.$message.success("");
            this.getUserList();
            this.addUserDialogVisible = false;
          } else {
            this.$message.error("");
          }
        }
      });
    },
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`users/${id}`);
      console.log(res);
      if (res.meta.status !== 200) {
        return $message.error("");
      }
      this.editForm = res.data;
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editRef.resetFields();
    },
    // 
    editUserInfo() {
      this.$refs.editRef.validate(async vaild => {
        if (vaild) {
          const { data: res } = await this.$http.put(
            `users/${this.editForm.id}`,
            {
              email: this.editForm.email,
              mobile: this.editForm.mobile
            }
          );
          if (res.meta.status !== 200) {
            return this.$message.error("");
          }
          this.$message.success("");
          this.getUserList();
          this.editDialogVisible = false;
        }
      });
    },
    // 
    deleteUser(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete(`users/${id}`);
          if (res.meta.status !== 200) {
            return this.$message({
              type: "error",
              message: "!"
            });
          }
          this.$message({
            type: "success",
            message: "!"
          });
          this.getUserList();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    async setRole(userInfo) {
      this.userRole = userInfo;
      const { data: res } = await this.$http.get(`roles`);
      console.log(res.data);
      if (res.meta.status !== 200) {
        this.$message.error("");
      }

      this.roulesList = res.data;

      this.setRoledialogVisible = true;
    },
    // 
    async setRoleHandel() {
      if (!this.rouleSelect) {
        this.$message.error("");
      }
      const { data: res } = await this.$http.put(
        `users/${this.userRole.id}/role`,
        {
          rid: this.rouleSelect
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("")
      this.setRoledialogVisible = false;
      this.rouleSelect = "";
    }
  }
};
</script>
<style lang="less" scoped>
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
}
</style>